import { BrowserRouter as Router, Route } from 'react-router-dom';
import Header from 'src/components/Header';
import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { fetchInitConfig } from 'src/app/thunks';
import Footer from 'src/components/Footer';
import Services from 'src/components/Services';
import { Tablet } from 'src/components/utils/Media';
import Page from 'src/components/Page';


class Routes extends React.Component {
  componentDidMount() {
    this.props.fetchInitConfig();
  }

  render() {
    return (
      <Router>
        <Header />
        <main>
          <Route
            path="/"
            render={({ location }) => {
              const path = location.pathname;
              const pageName = path === '/' ? 'Main' : path.replace('/', '');

              return <Page pageName={pageName} />;
            }}
          />
        </main>
        <Tablet>
          <Services />
        </Tablet>
        <Footer />
      </Router>
    );
  }
}

Routes.propTypes = {
  fetchInitConfig: PropTypes.func.isRequired,
};

const mapDispatchToProps = dispatch => ({
  fetchInitConfig: () => dispatch(fetchInitConfig()),
});

export default connect(null, mapDispatchToProps)(Routes);
