import React, { Component } from 'react';
import { connect } from 'react-redux';
import {
  AppBlock,
  AppBlockText,
  AppBlockWrap,
  Certificates,
  Copyright,
  FooterContact,
  FooterContacts,
  FooterContent,
  FooterLink,
  FooterLinks,
  FooterWrap,
  MapBlock,
  MapBlockText,
  PhoneBlock,
  PhoneBlockLinks,
  PhoneBlockNumber,
} from 'src/components/Footer/styled';
import { IconApp } from 'src/components/Icons/IconApp';
import {
  appUrlSelector,
  emailSelector,
  footerLinksSelector,
  locationLinkSelector,
  officeAddressSelector,
  socLinksSelector,
  supportNumberSelector,
  whatsAppSelector,
} from 'src/services/options/selectors';
import PropTypes from 'prop-types';
import { IconMarker } from 'src/components/Icons/IconMarker';
import { Link } from 'react-router-dom';
import { IconFacebook } from 'src/components/Icons/IconFacebook';
import { IconTwitter } from 'src/components/Icons/IconTwitter';
import { getOptionProps } from 'src/utils/utils';


class Footer extends Component {
  renderFooterLinks = () => this.props.footerLinks.map((link) => {
    const props = getOptionProps(link);
    return <FooterLink {...props}>{link.label}</FooterLink>;
  });

  renderSocLinks = () => this.props.socLinks.map(link => (
    <a key={link.id} href={link.value} rel="noopener noreferrer" target="_blank">
      {link.name === 'twitter_url' && <IconTwitter />}
      {link.name === 'facebook_url' && <IconFacebook />}
    </a>
  ));

  render() {
    const {
      appUrl, locationPath, officeAddress, supportNumber, email, whatsApp,
    } = this.props;
    return (
      <FooterWrap id="footer">
        <FooterContent>
          <FooterLinks className="visible-tablet-up">
            {this.renderFooterLinks()}
          </FooterLinks>

          <PhoneBlock className="hidden-tablet-up">
            <span>toll free for Uganda</span>
            <PhoneBlockNumber>{supportNumber}</PhoneBlockNumber>
            <PhoneBlockLinks>{this.renderSocLinks()}</PhoneBlockLinks>
          </PhoneBlock>

          <AppBlockWrap>
            <AppBlock>
              <a href={appUrl} rel="noopener noreferrer" target="_blank">
                <IconApp />
                <AppBlockText>
                  <span>Get from the</span>
                  <span>Google Play</span>
                </AppBlockText>
              </a>
            </AppBlock>
            <MapBlock>
              <Link to={locationPath}>
                <IconMarker />
                <MapBlockText>Find the nearest paypoints on the map</MapBlockText>
              </Link>
            </MapBlock>
          </AppBlockWrap>

          <FooterContacts className="visible-desktop-up">
            <FooterContact>
              Email:
              {' '}
              {email}
            </FooterContact>
            <FooterContact>
              WhatsApp:
              {' '}
              {whatsApp}
            </FooterContact>
            <PhoneBlockLinks>{this.renderSocLinks()}</PhoneBlockLinks>
          </FooterContacts>

          <Certificates className="hidden-tablet-up">
            <a href="/">Certificate 1</a>
            <a href="/">Certificate 2</a>
            <a href="/">Certificate 3</a>
          </Certificates>

          <Copyright>
            <span>
              &copy; 2009 -
              {new Date().getFullYear()}
              {' '}
              African Vending Systems Ltd.
            </span>
            <span>{officeAddress}</span>
          </Copyright>
        </FooterContent>
      </FooterWrap>
    );
  }
}

Footer.propTypes = {
  appUrl: PropTypes.string.isRequired,
  locationPath: PropTypes.string.isRequired,
  officeAddress: PropTypes.string.isRequired,
  supportNumber: PropTypes.string.isRequired,
  whatsApp: PropTypes.string.isRequired,
  email: PropTypes.string.isRequired,
  socLinks: PropTypes.arrayOf(PropTypes.object).isRequired,
  footerLinks: PropTypes.arrayOf(PropTypes.object).isRequired,
};

const mapDispatchToProps = state => ({
  appUrl: appUrlSelector(state),
  locationPath: locationLinkSelector(state).target || '',
  officeAddress: officeAddressSelector(state),
  supportNumber: supportNumberSelector(state),
  socLinks: socLinksSelector(state),
  footerLinks: footerLinksSelector(state),
  email: emailSelector(state),
  whatsApp: whatsAppSelector(state),
});

export default connect(mapDispatchToProps)(Footer);
