export const PopupContent = styled.div`
  position: absolute; 
  z-index: 40;
  left: 50%;
  width: 100vw;
  right: 0;
  top: 100%;
  padding: 20px;
  background: #fff;
  opacity: ${props => (props.visible ? 1 : 0)};
  box-shadow: ${boxShadow.card};
  transition: opacity .2s, transform .2s;
  pointer-events: ${props => (props.visible ? 'initial' : 'none')};
  transform: ${props => (props.visible ? 'translateY(0) translateX(-50%)' : 'translateY(-20px) translateX(-50%)')};
  
  @media(min-width: 768px) {
    position:absolute;
    top: calc(100% + 20px);
    left: 50%;
    transform: translateX(-50%);
    width: 300px;
    padding-top: 0;
  }
`;

