import styled from 'styled-components';
import { boxShadow, colors } from 'src/utils/constants';
import { PageContentWrap } from 'src/components/PageContent/styled';

export const Button = styled.a`
  display:flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content:center;
  border: 1px solid ${colors.border};
  background: #ffffff;
  padding: 10px;
  width: 50%;
  height: calc((100vw - 60px) / 2);
  max-width: 170px;
  max-height: 170px;
  cursor:pointer;
  transition: color .2s;
  box-shadow: ${boxShadow.card};
  
  &:nth-child(2n) {
    border-right: none;
  }
  &:nth-child(2n + 1),
  &:first-child {
    border-left: none;
  }
  &:first-child,
  &:nth-child(2) {
    border-top: none;
  }
  &:last-child,
  &:nth-last-child(2) {
    border-bottom: none; 
  }
  
  &:hover {
    color: ${colors.accent};
  }
  
  ${PageContentWrap} & {
    width: calc(100 / 3 * 1%); 
    max-height: 140px;
    border: 1px solid ${colors.border} !important;
    
  }
  
  
  @media(min-width: 768px) {
    width: calc(100 / 3 * 1%);
    height: calc(60vw * 0.33);
    max-width: 220px;
    max-height: 220px;
    
    &:nth-child(2n) {
      border-right: 1px solid ${colors.border};
    }
    &:nth-child(2n + 1) {
      border-left: 1px solid ${colors.border};
    }    
    &:nth-child(3) {
      border-top: none;
    }    
    &:nth-last-child(3) {
      border-bottom: none;
    }
  }
`;

export const ButtonsWrap = styled.div`
  display:flex;
  flex-wrap:wrap;
  width: calc(100vw - 60px);
  max-width: 340px;
  margin: 0 auto;
  
  ${PageContentWrap} & {
    margin: 20px auto;
    width: calc(100% - 40px);
    justify-content:center;
  }
  
  ${PageContentWrap} ~ & {
      
    @media(min-width: 1024px) {
    flex-flow: column nowrap;
    width: 160px;
    flex-shrink: 0;
    
      ${Button} {
        width: 100%;
        height: 160px;
        //box-shadow: none;
        border-bottom: 1px solid ${colors.border};
        border-right: none;
      }
    }
  }
  
  @media(min-width: 768px) {
    width: calc(60vw);
    max-width: 660px;
    margin-right: 0;
    margin-left: 0;
  }
`;


export const ButtonIcon = styled.div`
  background-repeat:no-repeat;
  background-position: center;
  background-size:contain;
  background-image: url(${props => props.icon});
  width: 50px;height: 50px;
  margin-bottom: 12px;
  transition: all .2s;
  
  ${Button}:hover & {
    transform: scale(1.1);
  }
  
  @media(min-width: 1024px) {
    width: 80px;
    height: 80px;
    margin-bottom: 20px;
  }
`;

export const ButtonTitle = styled.span`
  text-align:center;
  
  @media(min-width: 1024px) {
    font-size: 18px;
  }
`;
