/**
 * Filters 'services' in 'options' response object
 * Mutates the response object.
 * Picks out 10 random items.
 * @param {Array} options - api response object
 * @returns {undefined}
 */
export const filterServices = (options) => {
  const servicesObj = options.find(option => option.name === 'services');

  if (!servicesObj || !servicesObj.items.length) return;

  const services = servicesObj.items;
  const filteredServices = [];

  for (let i = 0; i < 10; i += 1) {
    const index = Math.floor(Math.random() * services.length);
    const service = services.splice(index, 1)[0];

    filteredServices.push(service);
  }

  servicesObj.items = filteredServices;
};
