import { createSelector } from 'reselect';

export const pagesSelector = state => state.pages;

const currentPageSelector = state => state.pages.current;

export const currentPageConfigSelector = createSelector(
  [currentPageSelector, pagesSelector],
  (pageName, pages) => pages[pageName] || { buttons: [] },
);

export const contactsContentSelector = createSelector(
  [pagesSelector],
  (pages) => {
    const contactsPageConfig = pages.Contacts;

    if (contactsPageConfig) return contactsPageConfig.content;
    return '';
  },
);

export const pageButtonsSelector = createSelector(
  [currentPageConfigSelector],
  pageConfig => pageConfig.buttons
    .filter(btn => btn.enabled)
    .sort((prev, next) => (prev.priority < next.priority ? -1 : 1)) || [],
);
