import { fetchAndStorePageConfig } from 'src/services/pages/thunks';
import { fetchOptions } from 'src/services/options/thunks';


// Request the data, required for the app on initial load
export const fetchInitConfig = () => (dispatch) => {
  dispatch(fetchOptions());
  // Fetch contacts page, because contacts page content needed
  // for contacts block in the header on each page
  dispatch(fetchAndStorePageConfig('Contacts'));
};
