import React from 'react';
import PropTypes from 'prop-types';
import { IconClose } from 'src/components/Icons/IconClose';
import { IconArrow } from 'src/components/Icons/IconArrow';
import { IconFacebook } from 'src/components/Icons/IconFacebook';
import { IconTwitter } from 'src/components/Icons/IconTwitter';
import {
  SideNavBtn,
  SideNavContent,
  SideNavHeader,
  SideNavLink,
  SideNavLinks,
  SideNavLogo,
  SideNavOverlay,
  SideNavSocLink,
  SideNavSocLinks,
  SideNavWrap,
} from 'src/components/SideNav/styled';
import { sideNavLinksSelector, socLinksSelector } from 'src/services/options/selectors';
import { connect } from 'react-redux';
import { Mobile, Tablet } from 'src/components/utils/Media';
import { Logo } from 'src/components/Logo';
import { getOptionProps } from 'src/utils/utils';


class SideNav extends React.Component {
  renderMainLinks = () => {
    const { mainLinks, toggleSideNav } = this.props;

    const links = mainLinks.map((link) => {
      const props = getOptionProps(link);

      return (
        <SideNavLink onClick={toggleSideNav} {...props}>
          {link.label}
          {props.to && <IconArrow tiny />}
        </SideNavLink>
      );
    });

    return <SideNavLinks>{links}</SideNavLinks>;
  };

  renderSocLinks = () => {
    const { socLinks, isOpen, toggleSideNav } = this.props;

    const links = socLinks.map((link) => {
      const props = {
        key: link.id,
        href: link.value,
        target: '_blank',
      };
      let icon;

      switch (link.name) {
        case 'facebook_url':
          icon = <IconFacebook />;
          break;
        case 'twitter_url':
          icon = <IconTwitter />;
          break;
        default:
          icon = null;
      }

      return <SideNavSocLink onClick={toggleSideNav} {...props}>{icon}</SideNavSocLink>;
    });

    return <SideNavSocLinks isOpen={isOpen}>{links}</SideNavSocLinks>;
  };

  render() {
    const { isOpen, toggleSideNav } = this.props;

    return (
      <SideNavWrap isOpen={isOpen}>
        <Mobile>
          <SideNavOverlay onClick={toggleSideNav} isOpen={isOpen} />

          <SideNavContent isOpen={isOpen}>

            <SideNavHeader>
              <IconClose onClick={toggleSideNav} />
              <SideNavLogo to="/" />
            </SideNavHeader>

            {this.renderMainLinks()}
            {this.renderSocLinks()}

          </SideNavContent>

        </Mobile>

        <Tablet>
          <Logo to="/" />
          <SideNavBtn isOpen={isOpen} onClick={toggleSideNav}>
            <span />
          </SideNavBtn>

          <SideNavContent isOpen={isOpen}>
            {this.renderMainLinks()}
          </SideNavContent>
          {this.renderSocLinks()}
        </Tablet>
      </SideNavWrap>
    );
  }
}

SideNav.propTypes = {
  mainLinks: PropTypes.arrayOf(PropTypes.object).isRequired,
  socLinks: PropTypes.arrayOf(PropTypes.object).isRequired,
  isOpen: PropTypes.bool.isRequired,
  toggleSideNav: PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
  mainLinks: sideNavLinksSelector(state),
  socLinks: socLinksSelector(state),
});
export default connect(mapStateToProps)(SideNav);
