import { SET_CURRENT_PAGE, STORE_PAGE_CONFIG } from 'src/services/pages/types';


const initialState = {
  current: '',
};


const reducer = (state = initialState, action) => {
  switch (action.type) {
    case STORE_PAGE_CONFIG:
      return {
        ...state,
        [action.pageName]: action.data,
      };
    case SET_CURRENT_PAGE:
      return {
        ...state,
        current: action.pageName,
      };
    default:
      return state;
  }
};


export default reducer;
