import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { PageWrap } from 'src/components/Page/styled';
import { pagesSelector } from 'src/services/pages/selectors';
import { fetchAndStorePageConfig } from 'src/services/pages/thunks';
import { setCurrentPage } from 'src/services/pages/actions';
import Buttons from 'src/components/Buttons';
import PageContent from 'src/components/PageContent';


class Page extends React.Component {
  constructor(props) {
    super(props);

    this.wrapRef = React.createRef();
  }

  componentDidMount() {
    this.props.handlePageUpdate();

    // Adding dynamic margin-top, because the header is fixed
    const wrapNode = this.wrapRef.current;
    wrapNode.style.marginTop = `${document.getElementById('header').clientHeight}px`;
  }

  componentDidUpdate(prevProps) {
    if (prevProps.pageName !== this.props.pageName) {
      this.props.handlePageUpdate();
    }
  }

  render() {
    const { pageConfig, pageName } = this.props;
    const {
      content, form, map, buttons,
    } = pageConfig;
    const isContentPage = content || form || map;
    const isButtonsPage = !isContentPage;

    return (
      <PageWrap ref={this.wrapRef}>
        {isContentPage && <PageContent pageName={pageName} pageConfig={pageConfig} />}
        {(isContentPage && !!buttons.length) && <Buttons className="visible-desktop-up" />}

        {isButtonsPage && <Buttons />}
      </PageWrap>
    );
  }
}

Page.propTypes = {
  pageName: PropTypes.string.isRequired,
  pageConfig: PropTypes.shape({
    buttons: PropTypes.array,
    content: PropTypes.string,
    form: PropTypes.object,
    map: PropTypes.bool,
  }),
  handlePageUpdate: PropTypes.func.isRequired,
};

Page.defaultProps = {
  pageConfig: {
    buttons: [],
  },
};

const mapStateToProps = (state, ownProps) => ({
  pageConfig: pagesSelector(state)[ownProps.pageName],
});

const mapDispatchToProps = (dispatch, ownProps) => ({
  handlePageUpdate: () => {
    dispatch(fetchAndStorePageConfig(ownProps.pageName));
    dispatch(setCurrentPage(ownProps.pageName));
  },
});


export default connect(mapStateToProps, mapDispatchToProps)(Page);
