import http from 'src/services/network';


/**
 * @param {String} data - Serialized form data
 */

export const checkPayment = (data) => {
  const url = `${API_URL}/payment/?${data}`;

  return http.get(url)
  // TODO: remove index
    .then(response => response.data[1]);
};

/**
 * @param {String} data - Serialized form data
 */
export const getYakaToken = (data) => {
  const url = `${API_URL}/yaka/?${data}`;

  return http.get(url)
  // TODO: remove index
    .then(res => res.data[0]);
};
