import styled from 'styled-components';
import { colors } from 'src/utils/constants';
import { Logo } from 'src/components/Logo';

export const SideNavWrap = styled.div`
  @media (min-width: 768px) {
    display:flex;
    flex-flow: column nowrap;
    width: 180px;
    position: fixed; 
    top: 0;left: 0;bottom: 0;
    padding: 10px;
    height:auto;
    z-index: ${props => (props.isOpen ? 10 : 0)};
    pointer-events: ${props => (props.isOpen ? 'initial' : 'none')};
    
    &:before {
      position: absolute;
      content: '';
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: #fff;
      box-shadow: 0 0 22px -4px rgba(0,0,0,.17);
      transform-origin: 100% 50%;
      transition: .5s cubic-bezier(.4,0,.2,1);
      transform: ${props => (props.isOpen ? 'none' : 'translateY(-100%) translateY(110px) skewY(-45deg)')};
      z-index: -1;
    }
  }
  
  @media (min-width: 1024px) {
    width: 240px; 
    padding: 20px;
  }
`;
export const SideNavOverlay = styled.div`
  position: fixed; 
  z-index: 50;
  top: 0;bottom: 0;left: 0;right: 0;
  background: rgba(0,0,0, 0.4);
  transition: opacity 0.3s;
  opacity: ${props => (props.isOpen ? 1 : 0)};
  pointer-events: ${props => (props.isOpen ? 'initial' : 'none')}; 
`;

export const SideNavContent = styled.div`
  display:flex;
  flex-flow: column nowrap;
  position:fixed;
  z-index: 51;
  top: 0;bottom: 0;
  left: 0;
  background: #ffffff;
  width: 280px;
  border-right: 1px solid ${colors.border};
  transition: transform .4s;
  transform: ${props => (props.isOpen ? 'translateX(0)' : 'translateX(-280px)')};
  
  @media(min-width: 768px) {
    overflow-y: auto;
    flex-grow: 1;
    background: none;   
    width: auto;
    border-right: none;
    position: initial;
    transition-delay: ${props => (props.isOpen ? '.3s' : 0)};
  }
`;
export const SideNavHeader = styled.div`
  display:flex;
  align-items: center;
  padding: 12px 20px 12px;
  border-bottom: 1px solid ${colors.border};
  pointer-events: initial !important;
`;
export const SideNavLogo = styled(Logo)`
  margin-left: auto;
  margin-right: auto;
`;
export const SideNavLinks = styled.div`
  padding: 15px 20px 50px;
  overflow-y: auto;
  
  @media(min-width: 768px) {
    padding-left: 0;padding-right: 0;
  }
`;
export const SideNavLink = styled.a`
  display: flex;
  align-items: center;
  justify-content:space-between;
  padding: 8px 25px 8px 0;
  
  @media(min-width: 768px) {
    padding: 12px 0 12px;
  }
`;
export const SideNavSocLinks = styled.div`
  flex: 1;
  background-color: ${colors.bg_grey};
  padding: 20px 20px 20px;
  text-align: center;
  
  @media(min-width: 768px) {
    flex:0;
    margin: auto -10px 0;
    padding-top: 30px;
    padding-bottom: 50px;
    border-top: 1px solid ${colors.border};
    transition: .3s cubic-bezier(.4,0,.2,1);
    transform: ${props => (props.isOpen ? 'none' : 'translateY(100%)')};
    transition-delay: ${props => (props.isOpen ? '.4s' : 0)}
  }
  
  @media(min-width: 1024px) {
    margin: auto -20px -20px;
  }
`;
export const SideNavSocLink = styled.a`
  display:inline-block; 
  margin-left: 8px;
  margin-right: 8px;
`;
export const SideNavBtn = styled.div`
  cursor:pointer;
  pointer-events: initial;
  
  @media(min-width: 768px) {
    width: 34px;
    height: 34px;
    position:relative;
    margin-top: 30px;
    margin-bottom: 8px;
    
    &:before,
    &:after {
      content: '';
      display: block;
      height: 2px;
      background: #000000;
      transition: transform .3s;
    }
    
    &:before {
      transform-origin: left top;
      transform: ${props => (props.isOpen ? 'rotate(45deg)' : 'none')};
    } 
    &:after {
      transform-origin: left bottom;
      transform: ${props => (props.isOpen ? 'rotate(-45deg)' : 'none')};
    } 
    
    span {
      display:block;
      height: 2px;
      background: #000000;
      margin: 10px 5px;
      opacity: ${props => (props.isOpen ? 0 : 1)};
      transition: opacity .2s;
    }
    
    @media(min-width: 1024px) {
      margin-bottom: 50px;
    }
  }
`;
