import React from 'react';
import { connect } from 'react-redux';
import { ServicesItem, ServicesWrap } from 'src/components/Services/styled';
import { servicesSelector } from 'src/services/options/selectors';
import PropTypes from 'prop-types';


const Services = ({ services }) => (
  <ServicesWrap>
    {services.map(service => (
      <ServicesItem
        key={service.id}
      >
        <img
          alt={service.name}
          title={service.name}
          src={`public/images/service/${service.name.toLowerCase()}.jpg`}
        />
      </ServicesItem>
    ))}
  </ServicesWrap>
);

Services.propTypes = {
  services: PropTypes.arrayOf(PropTypes.object).isRequired,
};

const mapStateToProps = state => ({
  services: servicesSelector(state),
});

export default connect(mapStateToProps)(Services);
