import React from 'react';
import Media from 'react-media';


// This components are used to render its content on specific devices.
export const Desktop = props => (
  // eslint-disable-next-line react/prop-types
  <Media query="(min-width: 1024px)" render={() => props.children} />
);

export const Tablet = props => (
  // eslint-disable-next-line react/prop-types
  <Media query="(min-width: 768px)" render={() => props.children} />
);

export const Mobile = props => (
  // eslint-disable-next-line react/prop-types
  <Media query="(max-width: 767px)" render={() => props.children} />
);
