import styled from 'styled-components';
import arrowIcon from 'public/icons/other/arrow.svg';
import { Icon } from './Icon';


export const IconArrow = styled(Icon)`
  background-image: url(${arrowIcon});
  width: 18px;
  height: 18px;
  transition: transform .3s;
  cursor: ${props => ((props.to || props.onClick) ? 'pointer' : 'default')};
  
  ${props => props.tiny && `
    width: 14px;
    height: 14px;
  `};
  
  transform: ${(props) => {
    let angle = 0;

    switch (props.direction) {
      case 'down':
        angle = 90;
        break;
      case 'left':
        angle = 180;
        break;
      case 'up':
        angle = -90;
        break;
      case 'right':
      default:
        angle = 0;
    }
    return `rotate(${angle}deg);`;
  }}
`;
