import React from 'react';
import PropTypes from 'prop-types';
import {
  PageBody,
  PageBodyContent,
  PageBodyForm,
  PageContentWrap,
  PageHeader,
  PageHeaderBack,
  PageHeaderTitle,
  PageLoader,
  ResponseClose,
  ResponseContent,
  ResponseField,
  ResponseFieldLabel,
  ResponseFieldValue,
  ResponseList,
  ResponseWrap,
} from 'src/components/PageContent/styled';
import { withRouter } from 'react-router-dom';
import { parseMarkdownToHtml, serializeForm } from 'src/utils/utils';
import Buttons from 'src/components/Buttons';
import Form from 'src/components/Form';
import { checkPayment, getYakaToken } from 'src/services/payment';
import Loader from 'src/components/Loader';


class PageContent extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isRequesting: false,
      responseList: null,
    };
  }

  submitForm = (event) => {
    const { pageName } = this.props;
    event.preventDefault();

    this.setState(() => ({ isRequesting: true }));

    switch (pageName) {
      case 'CheckPayment':
        checkPayment(serializeForm(event.target))
          .then((data) => {
            this.setState(() => ({ responseList: data, isRequesting: false }));
          });
        break;
      case 'MyLastYakaToken':
        getYakaToken(serializeForm(event.target))
          .then((data) => {
            this.setState(() => ({ responseList: data, isRequesting: false }));
          });
        break;
      default:
        console.warn('Unknown page form');
    }

    event.target.reset();
  };

  clearResponseList = () => {
    this.setState(() => ({ responseList: null }));
  };

  goBack = () => {
    this.props.history.goBack();
  };

  renderPageBodyContent = html => (
    <PageBodyContent
      dangerouslySetInnerHTML={{ __html: html }}
    />
  );

  renderFormResponse = () => (
    <ResponseWrap>
      <ResponseClose onClick={this.clearResponseList} />
      <ResponseList>
        <ResponseContent>
          {this.state.responseList.map(field => (
            <ResponseField key={`${field.name}_${field.value}`}>
              <ResponseFieldLabel>
                {field.label}
                <span>:</span>
              </ResponseFieldLabel>
              <ResponseFieldValue value={field.value}>{field.value}</ResponseFieldValue>
            </ResponseField>
          ))}
        </ResponseContent>
      </ResponseList>
    </ResponseWrap>
  );

  renderPageBodyForm = form => (
    <PageBodyForm>
      <Form
        form={form}
        onSubmit={this.submitForm}
      />
    </PageBodyForm>
  );

  renderPageLoader = () => (
    <PageLoader>
      <Loader />
    </PageLoader>
  );

  renderPageBody = () => {
    const { pageConfig } = this.props;
    const { responseList, isRequesting } = this.state;
    const { content, form } = pageConfig;

    return (
      <PageBody>
        {isRequesting && this.renderPageLoader()}
        {(responseList && !!responseList.length) && this.renderFormResponse()}
        {content && this.renderPageBodyContent(parseMarkdownToHtml(content))}
        {form && this.renderPageBodyForm(form)}
      </PageBody>
    );
  };

  render() {
    const { pageConfig } = this.props;
    const {
      title, content, form, buttons,
    } = pageConfig;

    return (
      <PageContentWrap>

        <PageHeader>
          <PageHeaderBack
            onClick={this.goBack}
            direction="left"
          />
          <PageHeaderTitle>{title}</PageHeaderTitle>
        </PageHeader>

        {!!buttons.length && <Buttons className="hidden-desktop-up" />}
        {(content || form) && this.renderPageBody()}
      </PageContentWrap>
    );
  }
}


PageContent.propTypes = {
  pageConfig: PropTypes.shape({
    title: PropTypes.string.isRequired,
    content: PropTypes.string,
    form: PropTypes.object,
    buttons: PropTypes.array,
  }),
  history: PropTypes.shape({
    goBack: PropTypes.func.isRequired,
  }).isRequired,
  pageName: PropTypes.string.isRequired,
};

PageContent.defaultProps = {
  pageConfig: {
    buttons: [],
  },
};

export default withRouter(PageContent);
