import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import {
  Button, ButtonIcon, ButtonsWrap, ButtonTitle,
} from 'src/components/Buttons/styled';
import { pageButtonsSelector } from 'src/services/pages/selectors';
import { getOptionProps } from 'src/utils/utils';


class Buttons extends React.Component {
  renderButtons = () => {
    const { buttons } = this.props;

    return buttons.map((button) => {
      const props = getOptionProps(button);

      return (
        <Button {...props}>
          <ButtonIcon icon={`public/icons/button/${(button.name || 'universal').toLowerCase()}.svg`} />
          <ButtonTitle>{button.label}</ButtonTitle>
        </Button>
      );
    });
  };

  render() {
    const { className } = this.props;

    return (
      <ButtonsWrap className={className}>
        {this.renderButtons()}
      </ButtonsWrap>
    );
  }
}

Buttons.propTypes = {
  buttons: PropTypes.arrayOf(PropTypes.object).isRequired,
  className: PropTypes.string,
};

Buttons.defaultProps = {
  className: '',
};

const mapStateToProps = state => ({
  buttons: pageButtonsSelector(state),
});


export default connect(mapStateToProps)(Buttons);
