import { createStore, applyMiddleware, compose } from 'redux';
import thunk from 'redux-thunk';
import reducer from './reducer';


const composeEnhancers = typeof window === 'object'
  && window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__
  ? window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ : compose;

// Saves redux store in local storage after updating the store via actions with 'isPersistent' flag
const storageMiddleware = ({ getState }) => next => (action) => {
  try {
    next(action);

    if (action.isPersistent) {
      window.localStorage.setItem('reduxState', JSON.stringify(getState()));
    }
  } catch (e) {
    console.log(e);
  }
};

const enhancer = composeEnhancers(
  applyMiddleware(thunk, storageMiddleware),
);

const getCachedState = () => {
  try {
    const stateAge = JSON.parse(window.localStorage.getItem('reduxStateAge'));

    // Invalidate the state each day
    if (stateAge === undefined || (Date.now() - stateAge) > (1000 * 60 * 60 * 24)) {
      window.localStorage.setItem('reduxStateAge', JSON.stringify(Date.now()));
      window.localStorage.removeItem('reduxState');

      return undefined;
    }

    return JSON.parse(window.localStorage.getItem('reduxState')) || undefined;
  } catch (e) {
    return undefined;
  }
};


const store = createStore(reducer, getCachedState(), enhancer);

export default store;
