import { createSelector } from 'reselect';
import { pageButtonsSelector } from 'src/services/pages/selectors';


export const optionsSelector = state => state.options;

export const linksSelector = createSelector(
  [optionsSelector],
  (options) => {
    const linksObject = options.find(e => e.name === 'links');

    if (linksObject) return linksObject.items;
    return [];
  },
);

export const headerLinksSelector = createSelector(
  [linksSelector],
  links => [...links]
    .filter(link => link.enabled)
    .sort((prev, next) => (prev.priority < next.priority ? -1 : 1)),
);

export const sideNavLinksSelector = createSelector(
  [linksSelector, pageButtonsSelector],
  (links, pageButtons) => [...links, ...pageButtons]
    .filter(link => link.enabled)
    .sort((prev, next) => (prev.priority < next.priority ? -1 : 1)),
);

export const socLinksSelector = createSelector(
  [optionsSelector],
  options => options.filter(option => option.name === 'facebook_url' || option.name === 'twitter_url'),
);

export const supportNumberSelector = createSelector(
  [optionsSelector],
  (options) => {
    const obj = options.find(e => e.name === 'support_number');

    if (obj) return obj.value;
    return '';
  },
);

export const appUrlSelector = createSelector(
  [optionsSelector],
  (options) => {
    const obj = options.find(e => e.name === 'android_app_url');

    if (obj) return obj.value;
    return '';
  },
);


export const locationLinkSelector = createSelector(
  [linksSelector],
  links => links.find(link => link.name === 'Location') || {},
);

export const officeAddressSelector = createSelector(
  [optionsSelector],
  (options) => {
    const obj = options.find(option => option.name === 'office_address');

    if (obj) return obj.value;
    return '';
  },
);

export const footerLinksSelector = createSelector(
  [pageButtonsSelector, linksSelector],
  (buttons, links) => [...buttons, ...links],
);

export const emailSelector = createSelector(
  [optionsSelector],
  (options) => {
    const obj = options.find(option => option.name === 'email_info');

    if (obj) return obj.value;
    return '';
  },
);

export const whatsAppSelector = createSelector(
  [optionsSelector],
  (options) => {
    const obj = options.find(option => option.name === 'whatsapp');

    if (obj) return obj.value;
    return '';
  },
);

export const servicesSelector = createSelector(
  [optionsSelector],
  (options) => {
    const obj = options.find(option => option.name === 'services');

    if (!obj || !obj.items.length) return [];
    return obj.items;
  },
);
