import http from 'src/services/network';
import { storePageConfig } from 'src/services/pages/actions';
import { pagesSelector } from 'src/services/pages/selectors';


const fetchPageConfig = (pageName) => {
  const url = `${SERVER_URL}/pages/?name=${pageName}`;

  return http.get(url)
    .then(response => response);
};

const injectMockData = (data, pageName) => {
  const { form } = data;
  let attrs = [];

  if (pageName === 'CheckPayment') {
    attrs = [
      {
        type: 'TEXT',
        label: 'Receipt ID',
        name: 'receiptId',
      },
      {
        type: 'TEXT',
        label: 'Terminal name',
        name: 'terminalName',
      },
    ];
  }

  if (pageName === 'MyLastYakaToken') {
    attrs = [
      {
        type: 'NUMBER',
        label: 'Meter Number',
        name: 'meterNo',
      },
    ];
  }

  form.attributes = attrs;
};

export const fetchAndStorePageConfig = pageName => (dispatch, getState) => {
  const pageConfig = pagesSelector(getState())[pageName];
  if (pageConfig !== undefined) return Promise.resolve();

  return fetchPageConfig(pageName)
    .then((response) => {
      const data = response.data[0];
      // TODO:remove
      if (data.form) injectMockData(data, pageName);
      // if (pageName === 'BecomeAnAgent') data.content = "#Requirements to be a PayWay Agent:\n##A Limited Liability Company\n  - 2 Passport size Photos\n  - Copy of valid National ID/Passport\n  - Copy of Trading License\n  - Recommendation Letter from LC1 Chairperson\n  - Certificate of Incorporation\n  - Form 7\n  - Cost of the agent POS terminal, Paper Rolls and Float as directed by the PayWay sales professional\n  - Photograph of where the business will be located\n#General Requirements for Individuals\n  - 2 Passport size photos\n  - Copy of National ID/ Passport\n  - Recommendation Letter from LC1 Chairperson\n  - Photograph of where the business will be located\n  - Cost of the agent POS terminal, Paper rolls and Float as directed by PayWay sales professional"
      // if (pageName === 'AboutUs') {
      //   data.buttons.push(data.buttons[0])
      // }
      // if (pageName === 'Main') {
      //   data.buttons = data.buttons.slice(0, 5);
      // }

      dispatch(storePageConfig(pageName, data));
    });
};
